.card-panel {
    padding: 20px
}

.card .card-title {
    font-size: 24px;
    font-weight: 300
}

.card .card-title.activator {
    cursor: pointer
}

.card.large .card-image,
.card.medium .card-image,
.card.small .card-image {
    max-height: 60%;
    overflow: hidden
}

.card.large .card-content,
.card.medium .card-content,
.card.small .card-content {
    max-height: 40%;
    overflow: hidden
}

.card.large .card-action,
.card.medium .card-action,
.card.small .card-action {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;
    background-color: inherit
}

.card .card-image,
.tabs,
.toast {
    position: relative
}

.card.small {
    height: 300px
}

.card.medium {
    height: 400px
}

.card.large {
    height: 500px
}

.card .card-image img {
    display: block;
    border-radius: 2px 2px 0 0;
    position: relative;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    width: 100%
}

.card .card-image .card-title {
    color: #fff;
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 20px
}

.card .card-content {
    padding: 20px;
    border-radius: 0 0 2px 2px
}

.card .card-content p {
    margin: 0;
    color: inherit
}

.card .card-content .card-title {
    line-height: 48px
}

.card .card-action {
    border-top: 1px solid rgba(160, 160, 160, .2);
    padding: 20px
}

.card .card-action a:not(.btn):not(.btn-large):not(.btn-floating) {
    color: #ffab40;
    margin-right: 20px;
    transition: color .3s ease;
    text-transform: uppercase
}

.card .card-action a:not(.btn):not(.btn-large):not(.btn-floating):hover {
    color: #ffd8a6
}

.card .card-reveal {
    padding: 20px;
    position: absolute;
    background-color: #fff;
    width: 100%;
    overflow-y: auto;
    top: 100%;
    height: 100%;
    z-index: 1;
    display: none
}

.card .card-reveal .card-title {
    cursor: pointer;
    display: block
}

#toast-container {
    display: block;
    position: fixed;
    z-index: 10000
}

@media only screen and (max-width:600px) {
    #toast-container {
        min-width: 100%;
        bottom: 0
    }
}

@media only screen and (min-width:601px) and (max-width:992px) {
    #toast-container {
        left: 5%;
        bottom: 7%;
        max-width: 90%
    }
    .toast {
        float: left
    }
}

@media only screen and (min-width:993px) {
    #toast-container {
        top: 10%;
        right: 7%;
        max-width: 86%
    }
}

.toast {
    border-radius: 2px;
    top: 0;
    width: auto;
    margin-top: 10px;
    max-width: 100%;
    height: auto;
    min-height: 48px;
    line-height: 1.5em;
    word-break: break-all;
    background-color: #323232;
    padding: 10px 25px;
    font-size: 1.1rem;
    color: #fff;
    display: flex;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-justify-content: space-between;
    -ms-flex-pack: justify;
    justify-content: space-between
}

.toast .btn,
.toast .btn-flat,
.toast .btn-large {
    margin: 0 0 0 3rem
}

.toast.rounded {
    border-radius: 24px
}

@media only screen and (max-width:600px) {
    .toast {
        width: 100%;
        border-radius: 0
    }
}

@media only screen and (min-width:993px) {
    .toast {
        float: right
    }
}

.tabs {
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    overflow-x: auto;
    overflow-y: hidden;
    height: 48px;
    background-color: #fff;
    margin: 0 auto;
    width: 100%;
    white-space: nowrap
}

.tabs .tab,
.tabs .tab a {
    display: block;
    text-overflow: ellipsis
}

.tabs .tab {
    -webkit-box-flex: 1;
    -webkit-flex-grow: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
    float: left;
    line-height: 48px;
    height: 48px;
    padding: 0;
    margin: 0;
    text-transform: uppercase;
    overflow: hidden;
    letter-spacing: .8px;
    width: 15%;
    min-width: 80px
}

.tabs .tab a {
    color: #ee6e73;
    width: 100%;
    height: 100%;
    overflow: hidden;
    transition: color .28s ease
}

.backdrop,
.material-tooltip {
    position: absolute;
    display: none;
    opacity: 0
}

.tabs .tab a:hover {
    color: #f9c9cb
}

.tabs .tab.disabled a {
    color: #f9c9cb;
    cursor: default
}

.tabs .indicator {
    position: absolute;
    bottom: 0;
    height: 2px;
    background-color: #f6b2b5;
    will-change: left, right
}

.material-tooltip {
    padding: 10px 8px;
    font-size: 1rem;
    z-index: 2000;
    background-color: transparent;
    border-radius: 2px;
    color: #fff;
    min-height: 36px;
    line-height: 120%;
    max-width: calc(100% - 4px);
    overflow: hidden;
    left: 0;
    top: 0;
    will-change: top, left
}

.backdrop {
    height: 7px;
    width: 14px;
    border-radius: 0 0 14px 14px;
    background-color: #323232;
    z-index: -1;
    -webkit-transform-origin: 50% 10%;
    transform-origin: 50% 10%;
    will-change: transform, opacity
}

.btn,
.btn-flat,
.btn-large {
    border: none;
    border-radius: 2px;
    display: inline-block;
    height: 36px;
    line-height: 36px;
    outline: 0;
    padding: 0 2rem;
    text-transform: uppercase;
    vertical-align: middle
